import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
function generateComponentFunc(path) {
    return resolve => require([`@/views${path}`], resolve)
}
const Blog = generateComponentFunc('/blog.vue')//入口页
const Home = generateComponentFunc('/home/home.vue')//首页
const Life = generateComponentFunc('/life/life.vue')//生活
const Echarts = generateComponentFunc('/echarts/echarts.vue')//echarts实例

export default new Router({
  routes: [
    {
      path: '/',
      alias: '/bolg',
      name: 'bolg',
      component: Blog,
      children:[
        {
          path:'/',
          name: 'home',
          component: Home,
          meta: {
            title: '首页'
          }
        },
        {
          path:'home',
          name: 'home',
          component: Home,
          meta: {
            title: '首页',
          }
        },
        {
          path:'life',
          name: 'life',
          component: Life,
          meta: {
            title: '生活',
          }
        }
      ]
    },
    {
      path:'/echarts',
      name:'echarts',
      component:Echarts,
      meta:{
        title:'echarts'
      }
    }
  ]
})



